<style lang="less">
  @import "../../styles/common.less";
  @import "../../styles/table.less";
  .operate-col div {
    display: inline-block;
    margin-right:3px;
  }
  .operate-col > .ivu-table-cell {
    padding:0 !important;
  }
  .demo-spin-col{
    height: 100px;
    position: relative;
    border: 1px solid #eee;
  }
  .detail-title div{
    padding: 0 15px;
    display: inline-block;
    /*text-decoration: underline;*/
  }
</style>
<template>
  <div class="single-page-shadow">
    <Row>
      <Card :dis-hover="true" shadow>
        <div slot="title" class="detail-title">
          <Icon type="ios-list-box-outline"></Icon>
          <div style="font-weight: bolder;">任务列表</div>
        </div>

        <Row>
          <Col span="24" class="padding-bottom-5">
          <!--<Form inline>-->
            <!--<FormItem>-->
              <!--<Select v-model="find.task_type" @on-change="queryData" placement="bottom-start" placeholder="请选择任务类型" style="width:200px">-->
                <!--<Option v-for="item in typeList" :value="item.value" :key="item.value" :disabled="item.disabled">{{ item.label }}</Option>-->
              <!--</Select>-->
            <!--</FormItem>-->
            <!--<FormItem>-->
              <!--<DatePicker format="yyyy-MM-dd HH:mm" type="datetimerange" placeholder="请选择查询的时间范围"-->
                          <!--v-model="find.picker" @on-change="find.picker=$event" style="width: 250px" :editable="false"></DatePicker>-->
            <!--</FormItem>-->
            <!--<FormItem>-->
              <!--<Button type="success" @click="queryData">查询</Button>-->
              <!--<Button type="primary" @click="queryCancel" class="margin-left-10">重置</Button>-->
              <!--<Button type="default" @click="getData" class="margin-left-10">刷新</Button>-->
            <!--</FormItem>-->
          <!--</Form>-->
          <div style="display: inline-block">
            <Select v-model="find.task_type" @on-change="queryData" placement="bottom-start" placeholder="请选择任务类型" style="width:200px">
              <Option v-for="item in typeList" :value="item.value" :key="item.value" :disabled="item.disabled">{{ item.label }}</Option>
            </Select>
          </div>
          <div style="display: inline-block;float:right;">
            <Button type="default" @click="getData" class="margin-left-10">刷新</Button>
          </div>
          <div style="display: inline-block;float:right;">
            <Button type="primary" @click="getData" class="margin-left-10">搜索</Button>
          </div>
          <div style="display: inline-block;float:right;">
            <Input v-model="search" placeholder="搜索" style="width: 250px" @on-keyup="startSearch">
              <Icon type="ios-search" slot="prefix" />
            </Input>
          </div>
          </Col>
          <Col span="24">
            <!--<Page show-total :total="totalNumber" :current="currentPage" show-elevator @on-change="pageChange" :page-size="page_size"></Page>-->
            <!--<br>-->
            <Table border :loading="tableLoading" :columns="columns" :data="tableData" stripe size="small">
            <template slot-scope="{ row }" slot="task_status">
              <Tag v-if="row.task_status === 'INIT'" color="default">准备中</Tag>
              <Tooltip v-if="row.task_status === 'FAILED'" theme="light" max-width="800" :content="row.task_message" placement="right">
                <Tag color="error">解析失败</Tag>
              </Tooltip>
              <Tag v-if="row.task_status === 'SUCCESS'" color="success">处理成功</Tag>
              <Tag v-if="row.task_status === 'PROCESSING'" color="primary">处理中</Tag>
            </template>
            <template slot-scope="{ row }" slot="sql">
              <div style="font-weight: bold;">{{ row.sql_need_optimize }}/{{ row.sql_total }}</div>
            </template>
            <template slot-scope="{ row }" slot="task_type">
              <div style="font-weight: bold;display: inline-block;">{{ typeObj[row.task_type] }}</div>
              <div v-if="row.valid ==='0' && isAdmin" style="display: inline-block;color:#d84a38;">测试</div>
            </template>
            <template slot-scope="{ row }" slot="tag_review_result">
              <div v-if=" row.tag_review_result === 'REVIWING' ||  row.tag_review_result === 'REVIEWING'">
                <Tag color="primary">审核中</Tag>
              </div>
              <div v-else-if=" row.tag_review_result === 'INIT'">
                <Tag color="default">初始化</Tag>
              </div>
              <div v-else-if=" row.tag_review_result === 'READY'">
                <Tag color="orange">待审核</Tag>
              </div>
              <div v-else-if=" row.tag_review_result === 'SUCCESS'">
                <Tag color="success">通过</Tag>
              </div>
              <div v-else-if=" row.tag_review_result === 'FAIL'">
                <Tag color="error">不通过</Tag>
              </div>
              <div v-else>未知{{ row.tag_review_result }}</div>
            </template>
            <template slot-scope="{ row }" slot="action">
              <div v-if="isAdmin">
                <Button type="primary" size="small" @click="reviewTaskPass(row)">通过</Button>
              </div>
              <div v-if="isAdmin">
                <Button type="error" size="small" @click="reviewTaskNoPass(row)">不通过</Button>
              </div>
              <div v-if="isAdmin">
                <Button type="warning" size="small" @click="reviewTaskInvalid(row)">测试</Button>
              </div>
              <div>
                <Button type="default" size="small" @click="openDetail(row)">详情</Button>
              </div>
            </template>
          </Table>
          </Col>
        </Row>
        <br>
        <Page show-total :total="totalNumber" :current="currentPage" show-elevator @on-change="pageChange" :page-size="page_size"></Page>
        <Divider />
      </Card>
    </Row>
  </div>
</template>
<script>
  import util from '../../libs/util'
  import { getTaskList, changeReviewResult, changeTaskInvalid } from '@/api/task';

  export default {
    name: 'TaskList',
    components: {
    },
    data () {
      return {
        tableLoading: false,
        typeList: util.taskTypeArr,
        typeObj: util.taskTypeObj,
        columns: [
          {
            title: '类型',
            key: 'task_type',
            slot: 'task_type'
          },
          {
            title: '状态',
            key: 'task_status',
            slot: 'task_status'
          },
          {
            title: 'Sql总数',
            key: 'sql_total'
          },
          {
            title: '待优化Sql总数',
            key: 'sql_need_optimize'
          },{
            title: '创建时间',
            key: 'created_at'
          },
        ],
        totalNumber: 0,
        currentPage: 1,
        page_size: 15,
        tableData: [],
        search: '',
        find: {
          task_type: ''
        }
      };
    },
    computed: {
      isAdmin() {
        return sessionStorage.getItem('auth').indexOf('admin') >= 0
      }
    },
    methods: {
      resetSearch() {
        this.search = ''
        this.getData()
      },
      startSearch(e) {
        if (e.keyCode === 13) {
          this.getData()
        }
      },
      reviewTaskPass(row) {
        console.log(row)
        this.$Modal.confirm({
          title: `确认将项目${row.app_name}，版本:${row.current_tag}，待Review的Sql(${row.sql_need_optimize}条)直接标记为全部通过吗`,
          'z-index': 1010,
          width: 700,
          // content: '请确认创建Review任务',
          onOk: () => {
            changeReviewResult({tag_review_result: 'SUCCESS', id: row.id}).then(res => {
              if (res.data.status === 'success') {
                this.$Message.success({content: '已经成功标记为通过', duration: 5})
              } else {
                this.$Message.error({content: '标记失败请稍后再试', duration: 5})
              }
            }).catch(e => {
              console.log(e)
              this.$Message.error({content: '标记失败请稍后再试', duration: 5})
            }).finally(() => {
              this.getData()
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });
      },
      reviewTaskInvalid(row) {

        this.$Modal.confirm({
          title: `确认将项目${row.app_name}，版本:${row.current_tag} 的标记为测试状态吗`,
          'z-index': 1010,
          width: 700,
          // content: '请确认创建Review任务',
          onOk: () => {
            changeTaskInvalid({valid: '0', task_id: row.task_id}).then(res => {
              console.log(res)
              if (res.data.status === 'success') {
                this.$Message.success({content: '已经成功标记为测试项目', duration: 5})
              } else {
                this.$Message.error({content: '标记失败请稍后再试', duration: 5})
              }
            }).catch(e => {
              console.log(e)
              this.$Message.error({content: '标记失败请稍后再试', duration: 5})
            }).finally(() => {
              this.getData()
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });
      },
      reviewTaskNoPass(row) {
        this.$Modal.confirm({
          title: `确认将项目${row.app_name}，版本:${row.current_tag} 的Review标记为不通过吗`,
          'z-index': 1010,
          width: 700,
          // content: '请确认创建Review任务',
          onOk: () => {
            changeReviewResult({tag_review_result: 'FAIL', id: row.id}).then(res => {
              console.log(res)
              if (res.data.status === 'success') {
                this.$Message.success({content: '已经成功标记为不通过', duration: 5})
              } else {
                this.$Message.error({content: '标记失败请稍后再试', duration: 5})
              }
            }).catch(e => {
              console.log(e)
              this.$Message.error({content: '标记失败请稍后再试', duration: 5})
            }).finally(() => {
              this.getData()
            })
          },
          onCancel: () => {
            // this.$Message.info('Clicked cancel');
          }
        });
      },
      getData() {

        if (sessionStorage.getItem('taskListFilter')) {
          this.find.task_type = sessionStorage.getItem('taskListFilter')
        } else {
          this.find.task_type = 'GITREPOSITORY'
          sessionStorage.setItem('taskListFilter', this.find.task_type)
        }
        if (sessionStorage.getItem('taskListPage')) {
          this.currentPage = parseInt(sessionStorage.getItem('taskListPage'))
        }
        this.tableLoading = true;

        if (this.find.task_type === 'SINGLE') {
          this.columns = util.taskListSingleColumns
        }
        if (this.find.task_type === 'GITREPOSITORY') {
          this.columns =  util.taskListGitColumns
        }

        const limit = this.page_size
        const offset = this.page_size * (this.currentPage - 1)
        getTaskList(limit, offset, this.find.task_type, '', '', '', '', '', this.search).then(res => {
          this.tableData = res.data.data
          this.totalNumber = res.data.count
        }).catch(e => {
          this.bus.$emit('errorHandler', e);
        }).finally(() => {
          this.tableLoading = false;
        })
      },
      openDetail(row) {
        util.openPage(this, 'task_detail', {task_id: row.task_id})
      },
      pageChange (p) {
        sessionStorage.setItem('taskListPage', p.toString())
        this.currentPage = p
        this.getData()
      },
      queryData () {
        sessionStorage.setItem('taskListFilter', this.find.task_type)
        sessionStorage.setItem('taskListPage', '1')
        // this.currentpage()
        this.getData()
      },
      queryCancel () {
        this.find.picker = []
        this.find.task_type = ''
        this.getData()
        // this.currentpage()
      }
    },
    mounted () {
      // this.currentpage();
      this.getData()
    },
    destroyed() {
    }
  };
</script>
